
@mixin h1{
    color:$dark100;
    line-height: 29.26px;
    font-size:24px;
    font-weight: 700;
}
@mixin h2{
    color:$dark100;
    line-height: 23.16px;
    font-size:19px;
    font-weight: 700;
}
@mixin h3{
    color:$dark100;
    line-height: 14.63px;
    font-size:12px;
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
}
@mixin body{
    color:$dark75;
    line-height: 24px;
    font-size:14px;
    font-weight: 400;
    font-style: normal;
}
@mixin material-icons{
    font-family: "Material Icons";
    font-size: 24px;
    line-height: 24px;
    font-weight: 400;
}